/**
 * Study object for a DPS Study data
 *
 * @module Slide
 */

 export default class Slide {
    constructor(slideId, slideInfo) {
        this._slideId = slideId;

        this._fileName = '';
        this._fileSize = 50;
        this._createdDate = '';
        this._createdTime = '';
        this._markerType = '';
        this._fileType = '';
        this._width = 0;
        this._height = 0;
        this._maxMagnification = '';
        this._deviceType = '';
        this._xRes = 0;
        this._yRes = 0;
        this._realWidth = 0;
        this._realHeight = 0;

        this._thumbnailPath = null;
        this._labelPath = null;

        this._arrKeyImages = [];

        if(slideInfo != null) {
            this._fileName = slideInfo.fileName;
            this._fileSize = slideInfo.fileSize;
            if(slideInfo.createdDate != undefined)
                this._createdDate = slideInfo.createdDate.substr(8, 2) + '/' + slideInfo.createdDate.substr(5, 2) + slideInfo.createdDate.substr(0, 4);
            this._createdTime = slideInfo.createdTime;
            this._markerType = slideInfo.markerTypeName;
            this._fileType = slideInfo.fileType;
            this._width = slideInfo.width;
            this._height = slideInfo.height;
            this._maxMagnification = slideInfo.maxMagnification;

            if(slideInfo.isMotic == true)
                this._deviceType = 'Motic';
            else
                this._deviceType = 'N/A';

            if(slideInfo.imagePath != null && slideInfo.imagePath != undefined)
                this._thumbnailPath = slideInfo.imagePath;
                
            if(slideInfo.labelPath != null && slideInfo.labelPath != undefined)
                this._labelPath = slideInfo.labelPath;

            this._xRes = slideInfo.xRes;
            this._yRes = slideInfo.yRes;
            this._realWidth = (this._width * this._xRes / 1e3).toFixed(1);
            this._realHeight = (this._height * this._yRes / 1e3).toFixed(1);
        }
    }

    setListKeyImages(keyImagesInfo) {
        if(keyImagesInfo == undefined || keyImagesInfo.length == 0)
            return;

        this._arrKeyImages = [];

        let len = keyImagesInfo.length;
        for(let i = 0; i < len; i++) {
            let obj = {};
            Object.assign(obj, keyImagesInfo[i]);
            this._arrKeyImages.push(obj);
        }
    }

    getFileSizeFormatted() {
        if (this._fileSize < 1000) {
          return this._fileSize;
        }
        let val = Math.round(this._fileSize);
        let s = val.toString();
        let s2 = '';
        let j = s.indexOf('.');
        if (j > -1) {
          s2 = s.substring(j);
        } else {
          j = s.length;
        }
    
        while (j > 3) {
          s2 = '.' + s.substring(j - 3, j) + s2;
          j -= 3;
        }
        s2 = s.substring(0, j) + s2;
        return s2;
    }

    showSlideDetail() {
        console.log('showSlideDetail');
        $('#lblSlideFileName').html(this._fileName);
        $('#lblSlideCreatedDate').html(this._createdDate);
        $('#lblSlideFileSize').html(this.getFileSizeFormatted());
        $('#lblSlideDevice').html(this._deviceType);

        //mm  mm
        $('#lblSlideRealSize').html(this._realWidth + 'mm x ' + this._realHeight + 'mm');

        if(this._width > 0 && this._height > 0)
            $('#lblSlideResolution').html(this._width + 'px x ' + this._height + 'px');

        $('#lblSlideFileType').html(this._fileType);

        if(this._maxMagnification > 0)
            $('#lblSlideMaxMagnification').html(this._maxMagnification);
        else
            $('#lblSlideMaxMagnification').html('N/A');
    }
}