import Constants from "../constants";
import Slide from "./slide";

/**
 * Study object for a DPS Study data
 *
 * @module Study
 */

 export default class Study {
    constructor(studyId, slidesInfo) {
        this._studyId = studyId;
        this._arrSlides = [];

        this._patientInfo = {};

        if(slidesInfo != null) {
            //main info
            this._patientInfo = {
                patientName: slidesInfo.patientsName,
                patientCode: slidesInfo.patientCode,
                patientId: slidesInfo.patientId,
                sex: slidesInfo.patientsSex,
                sexCode: (slidesInfo.patientsSex == Constants.PATIENT_SEX_CODE.FEMALE) ? 'F' : 'M',
                yob: slidesInfo.yob,
                age: this.getAge(slidesInfo.yob),
                address: slidesInfo.address,
                specimenCode: slidesInfo.specimensCode,
                visitCode: slidesInfo.visitCode,
                sourceHospital: slidesInfo.sourceHospital,
                clinicalDiagnosis: slidesInfo.clinicalDiagnosis,
                description: slidesInfo.description
            };

            //list slides
            if(slidesInfo.slides != undefined && slidesInfo.slides.length > 0) {
                for(let i = 0; i < slidesInfo.slides.length; i++) {
                    let slide = new Slide(slidesInfo.slides[i].id, slidesInfo.slides[i]);
                    this._arrSlides.push(slide);
                }
            }
        }

        
    }

    getSlide(slideId) {
        for(let i = 0; i < this._arrSlides.length; i++) {
            if(this._arrSlides[i] != undefined && this._arrSlides[i]._slideId === slideId) {
                return this._arrSlides[i];
            }
        }
        return null;
    }

    getAge(yob) {
        let curYear = new Date().getFullYear();
        return curYear - yob;
    }
}