export function fitByRatio(ratio = 0) {
    if(ratio == 0)
        GlobalVars.slideViewer.fitAuto();
    else {
        let pointer = GlobalVars.slideViewer._mainViewer.viewport.getCenter();
        GlobalVars.slideViewer.fitTo(pointer, ratio, true);
    }
}

