export function uploadXml() {
    console.log('uploadXml');
    if(GlobalVars.dlgUploadXml == null) {
        GlobalVars.dlgUploadXml = $("#dlgUploadXml").kendoWindow({
            minWidth: 360,
            width: 400,
            height: 180,
            title: "Open file XML",
            animation: true,
            visible: false,
            close : function onClose(e) {
                //
            },
            actions: [
                "Close"
            ],
        }).data("kendoWindow");

        $('#btnOpenXml').on('click', onOpenFileXml);
        $('#btnUploadXml').on('click', onOpenUploadXml);
    }

    GlobalVars.dlgUploadXml.center().open();
}

export function onOpenFileXml() {
    console.log('onOpenFileXml');
    let inputFile = document.getElementById("txtXmlFile");
    let fileToLoad = inputFile.files[0];
    if(typeof fileToLoad !== 'undefined') {
        GlobalVars.slideViewer.openXmlFile(fileToLoad);
    }
}

export function onOpenUploadXml() {
    console.log('onOpenUploadXml');
    let inputFile = document.getElementById("txtXmlFile");
    let fileToLoad = inputFile.files[0];
    if(typeof fileToLoad !== 'undefined') {
        GlobalVars.slideViewer.uploadXmlFile(fileToLoad, parent.uploadFileXml);        
    }
}