import GlobalVars from "../globalvars";

export default class Annotation {
    static ANNOTATION_Z_INDEX = {
        MAIN_OBJECT: 10,
        LABEL: 30,
        ANCHOR: 60
    };

    constructor(type, parent, fabricCanvas, uuid, position, zoomLevel) {
        this._type = type;
        this._parent = parent;

        this._id = '';
        this._uuid = uuid;
        this._label = "";
        this._note = ""; //key image's note

        this._fabricCanvas = fabricCanvas;
        this._fabricObj = null;
        this._fabricText = null;
        this._fabricTextShadow = null;

        this._strokeColor = "#539BDE"; //blue
        this._strokeColorActive = "#FFEB3B"; //yellow
        this._strokeWidth = 2;
        this._textColor = "#539BDE"; // blue
        this._textColorActive = "#FFEB3B"; // yellow
        this._textShadow = "#333333"; // black
        this._textSize = 16;

        //clone position, not use directly
        this._initPosition = {
            x: position.x,
            y: position.y
        };
        this._zoomLevel = zoomLevel;

        this._isActive = false;

        this._bounds = {
            x1: position.x,
            y1: position.y,
            x2: position.x,
            y2: position.y
        };

        if(this._type != GlobalVars.ANNOTATION_TYPE.POLYGON)
            this._init();
    }

    _init() {
        console.log('annotation init, type: ' + this._type);
        let origX = this._initPosition.x;
        let origY = this._initPosition.y;
        // console.log('origX: ' + origX + ', origY: ' + origY);

        let curZoom = this._fabricCanvas.getZoom();
        origX = origX * curZoom / this._zoomLevel;
        origY = origY * curZoom / this._zoomLevel;

        if(this._type === GlobalVars.ANNOTATION_TYPE.RECT || this._type === GlobalVars.ANNOTATION_TYPE.KEYIMAGE) {
            this._fabricObj = new fabric.Rect({
                left: origX,
                top: origY,
                width: 2 / curZoom,
                height: 2 / curZoom,
                fill: "",
                stroke: this._strokeColor,
                defaultStroke: this._strokeColor,
                type: "rect",
                uuid: this._uuid,
                strokeWidth: this._strokeWidth / curZoom,
                strokeWidthDefault: this._strokeWidth / curZoom,
                hasControls: true,
                selectable: true,
            });
        }
        else if(this._type === GlobalVars.ANNOTATION_TYPE.ELLIPSE) {
            this._fabricObj = new fabric.Ellipse({
                left: origX,
                top: origY,
                originX: "left",
                originY: "top",
                rx: 0,
                ry: 0,
                angle: 0,
                fill: "",
                stroke: this._strokeColor,
                defaultStroke: this._strokeColor,
                type: "ellipse",
                uuid: this._uuid,
                strokeWidth: this._strokeWidth / curZoom,
                strokeWidthDefault: this._strokeWidth / curZoom,
                hasControls: true,
                selectable: true,
            });
        }
        else if(this._type === GlobalVars.ANNOTATION_TYPE.LINE) {
            let points = [origX, origY, origX, origY];
            this._fabricObj = new fabric.LineArrow(
                points, 
                {
                    fill: this._strokeColor,
                    stroke: this._strokeColor,
                    defaultStroke: this._strokeColor,
                    strokeWidth: this._strokeWidth / curZoom,
                    strokeWidthDefault: this._strokeWidth / curZoom,
                    arrowSize: 3 / curZoom,
                    originX: "center",
                    originY: "center",
                    id: "arrow_line",
                    uuid: this._uuid,
                    type: "arrow",
                    selectable: true,
                    hasControls: true,
                }
            );
        }
        else if(this._type === GlobalVars.ANNOTATION_TYPE.RULER) {
            let points = [origX, origY, origX, origY];
            this._fabricObj = new fabric.Line(
                points, 
                {
                    strokeWidth: this._strokeWidth / curZoom,
                    fill: this._strokeColor,
                    stroke: this._strokeColor,
                    defaultStroke: this._strokeColor,
                    strokeWidth: this._strokeWidth / curZoom,
                    strokeWidthDefault: this._strokeWidth / curZoom,
                    originX: "center",
                    originY: "center",
                    id: "arrow_line",
                    uuid: this._uuid,
                    type: "ruler",
                    selectable: false,
                    hasControls: false,
                }
            );
        }
        else { //free drawing
            return; //don't add _fabricObj
        }

        this._fabricCanvas.add(this._fabricObj);

        if(this._type === GlobalVars.ANNOTATION_TYPE.RULER) {
            this.setLabel('0');
        }

        console.log('annotation init end');
    }

    setActive(isActive) {
        this._isActive = isActive;

        //TODO: set color
        if(isActive) {
            if(this._fabricObj != null)
                this._fabricObj.set("stroke", this._strokeColorActive);
            if(this._fabricText != null)
                this._fabricText.set("stroke", this._textColorActive);
        }
        else {
            if(this._fabricObj != null)
                this._fabricObj.set("stroke", this._strokeColor);
            if(this._fabricText != null)
                this._fabricText.set("stroke", this._textColor);
        }
    }

    onCreating(pointer) {
        // console.log('onCreating, (' + pointer.x + ', ' + pointer.y);
        let origX = this._initPosition.x;
        let origY = this._initPosition.y;

        if(this._type === GlobalVars.ANNOTATION_TYPE.RECT || this._type === GlobalVars.ANNOTATION_TYPE.KEYIMAGE) {
            if (origX > pointer.x) {
                this._fabricObj.set({ left: Math.abs(pointer.x) });
            }
            if (origY > pointer.y) {
                this._fabricObj.set({ top: Math.abs(pointer.y) });
            }

            this._fabricObj.set({ width: Math.abs(origX - pointer.x) });
            this._fabricObj.set({ height: Math.abs(origY - pointer.y) });
        }
        else if(this._type === GlobalVars.ANNOTATION_TYPE.ELLIPSE) {
            let rx = Math.abs(origX - pointer.x) / 2;
            let ry = Math.abs(origY - pointer.y) / 2;
            if (rx > this._fabricObj.strokeWidth) {
                rx -= this._fabricObj.strokeWidth / 2;
            }
            if (ry > this._fabricObj.strokeWidth) {
                ry -= this._fabricObj.strokeWidth / 2;
            }
            this._fabricObj.set({ rx: rx, ry: ry });

            if (origX > pointer.x) {
                this._fabricObj.set({ originX: "right" });
            } else {
                this._fabricObj.set({ originX: "left" });
            }
            if (origY > pointer.y) {
                this._fabricObj.set({ originY: "bottom" });
            } else {
                this._fabricObj.set({ originY: "top" });
            }
        }
        else if(this._type === GlobalVars.ANNOTATION_TYPE.LINE) {
            this._fabricObj.set({
                x2: pointer.x,
                y2: pointer.y,
            });
        }
        else if(this._type === GlobalVars.ANNOTATION_TYPE.RULER) {
            this._fabricObj.set({
                x2: pointer.x,
                y2: pointer.y,
            });

            //measure distance
            let ratio = this._parent._mainViewer.viewport._contentSize.x / this._parent._overlay._scale;
            let dx = (pointer.x - origX) * ratio;
            let dy = (pointer.y - origY) * ratio;
            let length = Math.sqrt(dx * dx + dy * dy);

            let mpp = this._parent._mainViewer.scalebarInstance.pixelsPerMeter;
            length = (length / mpp) * 1e3;
            length = length.toFixed(3);
            while (length[length.length - 1] == "0" && length[length.length - 2] != ".")
                length = length.slice(0, length.length - 1);

            let label = length + 'mm';
            this.setLabel(label);
        }

        //update bounds
        if(pointer.x < this._bounds.x1)
            this._bounds.x1 = pointer.x;
        if(pointer.x > this._bounds.x2)
            this._bounds.x2 = pointer.x;
        if(pointer.y < this._bounds.y1)
            this._bounds.y1 = pointer.y;
        if(pointer.y > this._bounds.y2)
            this._bounds.y2 = pointer.y;
    }

    onFinish() {
        console.log('onFinish');
        if(this._type === GlobalVars.ANNOTATION_TYPE.KEYIMAGE) {
            this.showKeyImageDialog();
        }
        else if(this._type !== GlobalVars.ANNOTATION_TYPE.RULER) {
            this.showLabelDialog();
        }
        else { //ruler
            this._parent.onAnnotationCreated(this._uuid, this._label);
        }
    }

    onMove(fabricObj) {
        let eps = 1/this._fabricCanvas.getZoom();
        if(fabricObj.type != 'text') {
            this._fabricText.set({left: this._fabricObj.left});
            this._fabricText.set({top: this._fabricObj.top - (this._textSize + 4) / this._fabricCanvas.getZoom()}); 
            this._fabricTextShadow.set('left', this._fabricObj.left + eps);
            this._fabricTextShadow.set('top', this._fabricText.top + eps);
            this._fabricText.setCoords();
            this._fabricTextShadow.setCoords();
        }
        else { //moving the text object
            // this._fabricObj.set('left', fabricObj.left);
            // this._fabricObj.set('top', fabricObj.top + (this._textSize + 4) / this._fabricCanvas.getZoom()); 
            this._fabricTextShadow.set('left', fabricObj.left + eps);
            this._fabricTextShadow.set('top', fabricObj.top + eps);
            // this._fabricObj.setCoords();
            this._fabricTextShadow.setCoords();
        }
    }

    showLabelDialog() {
        if(GlobalVars.dlgAnnotationLabel == null) {
            var _parent = this._parent;
            GlobalVars.dlgAnnotationLabel = $("#annotationDialog").kendoWindow({
                minWidth: 380,
                width: 400,
                height: 120,
                title: "Annotation label",
                animation: true,
                visible: false,
                close : function onClose(e) {
                    $("#annotation_text").off('keydown');
                    let label = $("#annotation_text").val();
                    let uuid = $("#annotation_uuid").val();
                    if (label == undefined || label === "") {
                        _parent.removeAnnotation(uuid);
                    }
                    else {
                        _parent.onAnnotationCreated(uuid, label);
                    }
                    _parent.refreshFabricCanvas();
                },
                actions: [
                    "Close"
                ],
            }).data("kendoWindow");
        }

        //prepare data
        var _self = this;
        $("#annotation_uuid").val(this._uuid);
        $("#annotation_text").val(this._label);
        $("#annotation_text").on('keydown', function (e) {
            if (e.keyCode == 13) {
                let label = $("#annotation_text").val();
                if(label !== "")
                    _self.setLabel(label);
                // createAnnotationLabel.confirm(obj, $("#annotation_text").val());
                GlobalVars.dlgAnnotationLabel.close();
            } else if (e.keyCode == 27) {
                GlobalVars.dlgAnnotationLabel.close();
            }
            if (parseInt(e.key)) {
                e.stopPropagation();
            }
        });
        setTimeout(function () { document.getElementById('annotation_text').focus() }, 200);

        GlobalVars.dlgAnnotationLabel.center().open();
        
    }

    showKeyImageDialog() {
        var _self = this;
        if(GlobalVars.dlgKeyImageDetail == null) {
            GlobalVars.dlgKeyImageDetail = $("#dlgKeyImageDetail").kendoWindow({
                minWidth: 380,
                width: 400,
                height: 240,
                title: "Key image detail",
                animation: true,
                visible: false,
                close : function onClose(e) {
                    $("#btnSaveKeyImage").off('click');
                    $("#btnCloseKeyImage").off('click');
                },
                actions: [
                    "Close"
                ],
            }).data("kendoWindow");
        }

        //prepare data
        $("#txtKeyImageTitle").val(this._label);
        $("#txtKeyImageNote").val(this._note);
        $("#btnSaveKeyImage").on('click', function (e) {
            let label = $("#txtKeyImageTitle").val();
            if(label !== "") {
                _self.setLabel(label);
                _self._note = $("#txtKeyImageNote").val();
                let viewer = _self._parent._mainViewer;

                //openseadragon rect???
                let zoom = _self._fabricCanvas.getZoom();
                let selectionRect = {
                    x: _self._fabricObj.left / zoom,
                    y: _self._fabricObj.top / zoom,
                    width: _self._fabricObj.width / zoom,
                    height: _self._fabricObj.height / zoom
                };
                console.log('zoom: ' + zoom);
                console.log('selectionRect: (' + selectionRect.x + ', ' + selectionRect.y + ', ' + selectionRect.width + ', ' + selectionRect.height + ')');
                //screen rect?
                let windowRect = viewer.viewport.viewportToImageRectangle(viewer.viewport.getBounds());
                _self._parent.createKeyImage(selectionRect, windowRect, _self._label, _self._note);

                //notification success

                //close dlg
                GlobalVars.dlgKeyImageDetail.close();
            }
            else {
                //alert warning
            }
                
        });
        $("#btnCloseKeyImage").on('click', function (e) {
            _self._parent.removeAnnotation(_self._uuid);
            GlobalVars.dlgKeyImageDetail.close();
        });
        setTimeout(function () { document.getElementById('txtKeyImageTitle').focus() }, 200);

        GlobalVars.dlgKeyImageDetail.center().open();
        
    }

    setLabel(label, labelLeft = undefined, labelTop = undefined) {
        this._label = label;
        let left, top;

        let curZoom = this._fabricCanvas.getZoom();

        if(labelLeft != undefined)
            left = labelLeft;
        else if(this._fabricObj != null)
            left = this._fabricObj.left;

        if(labelTop != undefined)
            top = labelTop;
        else if(this._fabricObj != null)
            top = this._fabricObj.top - (this._textSize + 4) / curZoom;

        let eps = 1/curZoom;
        if(this._fabricText == null) {
            this._fabricTextShadow = new fabric.Text(label, {
                fontSize: this._textSize / curZoom,
                defaultFontSize: this._textSize / curZoom,
                fill: this._textShadow,
                originalColor: this._textShadow,
                strokeWidth: 1 / curZoom,
                type: "text",
                selectable: false,
                left: left + eps,
                top: top + eps,
                fontFamily: "arial",
                uuid: this._uuid,
                source: this._fabricObj,
            });
            this._fabricCanvas.add(this._fabricTextShadow);

            this._fabricText = new fabric.Text(label, {
                fontSize: this._textSize / curZoom,
                defaultFontSize: this._textSize / curZoom,
                fill: this._textColorActive,
                originalColor: this._textColorActive,
                strokeWidth: 1 / curZoom,
                type: "text",
                selectable: true,
                hasControls: false,
                left: left,
                top: top,
                fontFamily: "arial",
                uuid: this._uuid,
                source: this._fabricObj,
            });
            this._fabricCanvas.add(this._fabricText);
        }
        else {
            this._fabricText.set('text', label);
            this._fabricTextShadow.set('text', label);
        }
    }

    dispose() {
        this._fabricCanvas.remove(this._fabricObj);
        if(this._fabricText != null) {
            this._fabricCanvas.remove(this._fabricText);
            this._fabricCanvas.remove(this._fabricTextShadow);
        }
        this._fabricObj = null;
        this._fabricText = null;
        this._fabricTextShadow = null;
    }

    setFreeDrawingPath(path) {
        this._fabricObj = path;
        this._fabricObj.set({ uuid: this._uuid });
    }

    show() {
        if(this._fabricObj != null)
            this._fabricObj.visible = true;
        if(this._fabricText != null) {
            this._fabricText.visible = true;
            this._fabricTextShadow.visible = true;
        }
    }

    hide() {
        if(this._fabricObj != null)
            this._fabricObj.visible = false;
        if(this._fabricText != null) {
            this._fabricText.visible = false;
            this._fabricTextShadow.visible = false;
        }
    }

    showInsideBounds() {
        //nothing for now
    }

    getAnnotationData4Server() { 
        let dataObj = {};
        if(this._fabricObj == null) {
            return dataObj;
        }

        let attributes = [
            "Left",
            "Top",
            "Width",
            "Height",
            "OriginX",
            "OriginY",
            "Rx",
            "Ry",
            "Angle",
            "Fill",
            "DefaultStroke",
            "StrokeWidthDefault",
            "HasControls",
            "Path",
            "PathLeft",
            "PathTop",
            "PathOriginX",
            "PathOriginY",
            "LineLeft",
            "LineTop",
            "LineX1",
            "LineX2",
            "LineY1",
            "LineY2",
            "IsDeleted"
        ];

        for (let i = 0; i < attributes.length; i++) {
            let attr = attributes[i];
            dataObj[attr] = this._fabricObj[this.decapitalizeFirstLetter(attr)] || "";
        }

        //special float?
        dataObj["StrokeWidthDefault"] = parseFloat(this._fabricObj.strokeWidthDefault);

        dataObj["Label"] = this._label;
        if(this._fabricText != null) {
            dataObj["LabelLeft"] = this._fabricText.left;
            dataObj["LabelTop"] = this._fabricText.top;
            dataObj["LabelDefaultFontSize"] = this._fabricText.defaultFontSize;
            dataObj["LabelColor"] = this._textColorActive;
            dataObj["LabelSelectable"] = this._fabricText.selectable;
            dataObj["LabelFont"] = this._fabricText.fontFamily;
            // console.log('label info: ' + this._fabricText.left + ', ' + this._fabricText.top);
        }

        dataObj["Id"] = this._id;
        dataObj["UUID"] = this._uuid;
        dataObj["Type"] = this._type;

        if(this._type === GlobalVars.ANNOTATION_TYPE.FREEHAND) {
            let path = this._fabricObj;
            console.log(path);

            dataObj["Path"] = path.getPath();
            dataObj["PathLeft"] = path.left;
            dataObj["PathTop"] = path.top;
            dataObj["PathOriginX"] = path.originX;
            dataObj["PathOriginY"] = path.originY;
        }
        else if(this._type === GlobalVars.ANNOTATION_TYPE.LINE || this._type === GlobalVars.ANNOTATION_TYPE.RULER) {
            var p = this._fabricObj;
            this._fabricObj.setCoords();
            dataObj["LineX1"] = this._fabricObj.x1;
            dataObj["LineX2"] = this._fabricObj.x2;
            dataObj["LineY1"] = this._fabricObj.y1;
            dataObj["LineY2"] = this._fabricObj.y2;
            // console.log('line info: ' + p.x1 + ', ' + p.y1 + ', ' + p.x2 + ', ' + p.y2);
        }
        else if(this._type === GlobalVars.ANNOTATION_TYPE.ELLIPSE) {
            dataObj["Rx"] = this._fabricObj.getRx();
            dataObj["Ry"] = this._fabricObj.getRy();
        }

        dataObj.zoomLevel = this._zoomLevel;

        // console.log(dataObj);

        return dataObj;
    }

    decapitalizeFirstLetter(text) {
        return text && text[0].toLowerCase() + text.slice(1) || text;
    }

    static createFromDataObj(dataObj, parent, fabricCanvas) {
        // let zoomRatio = fabricCanvas.getZoom();
        let anno = new Annotation(parseInt(dataObj.type), parent, fabricCanvas, dataObj.uuid, {
            x: dataObj.left,
            y: dataObj.top
        }, dataObj.zoomLevel);

        let ratio = dataObj.zoomLevel / fabricCanvas.getZoom();

        //properties depends on type
        if(anno._type === GlobalVars.ANNOTATION_TYPE.RECT || anno._type === GlobalVars.ANNOTATION_TYPE.KEYIMAGE) {
            anno._fabricObj.set({ 
                width: dataObj.width,
                height: dataObj.height
            });
        }
        else if(anno._type === GlobalVars.ANNOTATION_TYPE.ELLIPSE) {
            anno._fabricObj.set({ 
                rx: dataObj.rx, 
                ry: dataObj.ry,
                originX: dataObj.originX,
                originY: dataObj.originY
            });
        }
        else if(anno._type === GlobalVars.ANNOTATION_TYPE.LINE || anno._type === GlobalVars.ANNOTATION_TYPE.RULER) {
            anno._fabricObj.set({
                x1: dataObj.lineX1,
                y1: dataObj.lineY1,
                x2: dataObj.lineX2,
                y2: dataObj.lineY2,
            });
            // anno._fabricObj.set({
            //     top: dataObj.top,
            //     left: dataObj.left,
            //     width: dataObj.width,
            //     height: dataObj.height,
            // });
            anno._fabricObj.setCoords();
        }

        //label
        anno.setLabel(dataObj.label, dataObj.labelLeft, dataObj.labelTop);

        anno.setActive(false);

        return anno;
    }

    updateSizeByZoom(curZoom) {
        let ratio = (curZoom > 2) ? (1.0 / curZoom) : 0.5;
        if(this._fabricObj != null) {
            this._fabricObj.set('strokeWidth', this._strokeWidth * ratio);
            if(this._type == GlobalVars.ANNOTATION_TYPE.ARROW || this._type == GlobalVars.ANNOTATION_TYPE.LINE) {
                this._fabricObj.set('arrowSize', 3 * ratio);
            }
        }
        if(this._fabricText != null) {
            this._fabricText.set({
                strokeWidth: ratio,
                defaultFontSize: this._textSize * ratio
            });
            this._fabricTextShadow.set({
                strokeWidth: ratio,
                defaultFontSize: this._textSize * ratio,
                left: this._fabricText.left + ratio,
                top: this._fabricText.top + ratio,
            });
        }
    }
}